<template>
  <v-app>
    <NavbarMenu />
    <div class="content">
      <v-container style="margin-top: 30px" class="mb-12">
        <v-card
          v-for="(ques, index) in questions"
          :key="index"
          class="ques_item"
        >
          <v-container fluid>
            <p>
              <span>{{ index + 1 }}.[{{ ques.diseaseName }}]</span>
              {{ ques.questionInfo }}({{ ques.score }} 分)
            </p>
            <!-- <v-radio-group v-model="choice"> -->
            <!-- <v-radio-group> -->
            <v-radio-group v-model="rs[index].value">
              <v-radio
                :label="ques.choiceA"
                color="red"
                value="A"
              ></v-radio>
              <!-- @change="changeResult(ques.questionId,value)" -->
              <v-radio
                :label="ques.choiceB"
                color="red"
                value="B"
              ></v-radio>
              <v-radio
                :label="ques.choiceC"
                color="red"
                value="C"
              ></v-radio>
              <v-radio
                :label="ques.choiceD"
                color="red"
                value="D"
              ></v-radio>
            </v-radio-group>
          </v-container>
        </v-card>
        <div class="text-center">
          <v-btn
            rounded
            color="primary"
            dark
            @click="getscore()"
            style="margin-top: 20px"
          >
            查看分数
          </v-btn>
        </div>
      </v-container>
    </div>
  </v-app>
</template>

<script>
import axios from "axios";
import qs from "qs";
export default {
  name: "ExamItem",

  answer: [],
  components: {
    NavbarMenu: () => import("../components/NavbarMenu"),
  },
  data() {
    return {
      choice: [],
      // rsesults: [],
      questions: [
        // {
        //   questionId: "011",
        //   questionInfo: "题干1",
        //   diseaseName: "病种名1",
        //   score: "10",
        //   // choice: [{ choice: "aaa1",value: "A" },{ choice: "",value: 1 },{ choice: "",value: 1 },{ choice: "",value: 1 },],
        //   choiceA: "aaa1",
        //   choiceB: "bbb1",
        //   choiceC: "ccc1",
        //   choiceD: "ddd1",
        // },
        // {
        //   questionId: "012",
        //   questionInfo: "题干2",
        //   diseaseName: "病种名2",
        //   score: "10",
        //   choiceA: "aaa2",
        //   choiceB: "bbb2",
        //   choiceC: "ccc2",
        //   choiceD: "ddd2",
        // },
      ],
      rs: [],
    };
  },
  created(){
    this.updata(); 
  },
  mounted() {
    console.log("id:",this.$route.query.id)
    this.questions.forEach((item) => {
      this.rs.push({
        questionId: item.questionId,
        value: "",
      });
    });
    console.log('rs:',this.rs)
  },
  methods: {
    updata() {
      axios
        .post("/test/paper", qs.stringify(
          {'testId':this.$route.query.id}
          ))
        .then((res) => {
          console.log("res=>", res);
          if (res.data.code == 200) {
            this.questions = res.data.data;

            console.log(res.data.data);
          }
          else {
            this.$store.dispatch('snackbar/openSnackbar',{
              msg:res.data.msg,
              color:'primary'
            })
          }
        })
        .catch((err) => {
          console.log(err.data);
        });
    },
    getscore() {
      console.log(this.rs);
      axios.post('/test/score',qs.stringify({
        result:this.rs
      })).then((res)=>{
        console.log("res:",res)
        if (res.data.code === 200){
          this.$store.dispatch('snackbar/openSnackbar',{
            msg:'您的分数为：'+res.data.data.score+' / '+res.data.data.sumscore,
            color:'primary'
          })
        }
        else{
          console.log('错误代码：',res.data.code)
        }
      }).catch((err) => {
          console.log("err=>", err);
        });
      // console.log(this.questions[index]);
      // console.log(this.choice);
    },
  },
};
</script>


